/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { useSelect } from '@wordpress/data';
import { registerBlockType } from '@wordpress/blocks';
import {
	BlockControls,
	InspectorControls,
	InnerBlocks,
	useBlockProps,
} from '@wordpress/block-editor';
import { PanelBody, BaseControl } from '@wordpress/components';
import { useInnerBlocksProps } from '@compatible';

/**
 * @Internal dependencies
 */
import metadata from './block.json';
import blockIcon from './icon';
import example from './_example';
import deprecated from './deprecated';
import {
	ArkbMarginControl,
	ArkbDeviceTab,
	ArkbGapControls,
	ArkbColumnCountControl,
	DynamicStyles,
} from '@components';
import { convertStylesToCssCode, getColumnBasis } from '@helper';

/**
 * @Others dependencies
 */
import classnames from 'classnames';

/**
 * style生成
 */
function getDynamicStyles({ gap, gapTab, gapMB, columnCount, clientId }) {
	const styles = {
		all: {
			'--arkb-gap--x': gap?.x || '',
			'--arkb-gap--y': gap?.y || '',
			'--arkb-box-width': getColumnBasis(columnCount?.pc) || '50%',
		},
		sp: {
			'--arkb-gap--x': gapTab?.x || '',
			'--arkb-gap--y': gapTab?.y || '',
			'--arkb-box-width': getColumnBasis(columnCount?.tab) || '50%',
		},
		mb: {
			'--arkb-gap--x': gapMB?.x || '',
			'--arkb-gap--y': gapMB?.y || '',
			'--arkb-box-width': getColumnBasis(columnCount?.mobile) || '100%',
		},
	};

	return convertStylesToCssCode(`#block-${clientId}`, styles);
}

/**
 * registerBlockType
 */
const blockName = 'ark-block-boxLinks';
registerBlockType(metadata.name, {
	title: __('Box links', 'arkhe-blocks'),
	icon: blockIcon,
	attributes: metadata.attributes,
	example,
	edit: ({ attributes, setAttributes, isSelected, clientId }) => {
		const { columnCount, gap, gapTab, gapMB } = attributes;

		// インナーブロックが選択されているかどうか
		const isChildSelected = useSelect(
			(select) => select('core/block-editor').hasSelectedInnerBlock(clientId, false),
			[clientId]
		);

		const blockProps = useBlockProps({
			className: classnames(blockName, 'ark-has-guide'),
		});

		// 左右marginの関係でカラムブロックは一つdivかませる
		const innerBlocksProps = useInnerBlocksProps(blockProps, {
			allowedBlocks: ['arkhe-blocks/box-link'],
			template: [['arkhe-blocks/box-link'], ['arkhe-blocks/box-link']],
			templateLock: false,
			orientation: 'horizontal',
			renderAppender: isSelected || isChildSelected ? InnerBlocks.ButtonBlockAppender : false,
		});

		const dynamicStyles = getDynamicStyles({ gap, gapTab, gapMB, columnCount, clientId });

		return (
			<>
				{dynamicStyles && <DynamicStyles style={dynamicStyles} />}
				<BlockControls>
					<ArkbMarginControl {...{ className: attributes.className, setAttributes }} />
				</BlockControls>
				<InspectorControls>
					<PanelBody title={__('Settings', 'arkhe-blocks')} initialOpen={true}>
						<BaseControl>
							<BaseControl.VisualLabel>
								{__('Number of columns', 'arkhe-blocks')}
							</BaseControl.VisualLabel>
							<ArkbColumnCountControl {...{ columnCount, setAttributes, max: 4 }} />
						</BaseControl>
						<BaseControl>
							<BaseControl.VisualLabel>
								{__('Margin between columns', 'arkhe-blocks')}
							</BaseControl.VisualLabel>

							<ArkbDeviceTab
								className='-gap -triple'
								controlPC={
									<ArkbGapControls
										value={gap}
										name='gap'
										setAttributes={setAttributes}
										defaultGap={{ x: '1.5rem', y: '1.5rem' }}
									/>
								}
								controlTab={
									<ArkbGapControls
										value={gapTab}
										name='gapTab'
										setAttributes={setAttributes}
										successionHelp={__('PC setting', 'arkhe-blocks')}
									/>
								}
								controlMB={
									<ArkbGapControls
										value={gapMB}
										name='gapMB'
										setAttributes={setAttributes}
										successionHelp={__('Tablet setting', 'arkhe-blocks')}
									/>
								}
							/>
						</BaseControl>
					</PanelBody>
				</InspectorControls>
				<div {...innerBlocksProps} />
			</>
		);
	},

	save: () => {
		const blockProps = useBlockProps.save({
			className: blockName,
		});

		return (
			<div {...blockProps}>
				<InnerBlocks.Content />
			</div>
		);
	},
	deprecated,
});
