/**
 * @WordPress dependencies
 */
import { InnerBlocks, useBlockProps } from '@wordpress/block-editor';
import { getColumnBasis } from '@helper';

/**
 * 変数
 */
const blockName = 'ark-block-boxLinks';

export default [
	{
		// css を <style>で
		supports: {
			anchor: true,
			className: false,
			align: ['wide'],
		},
		attributes: {
			columnCount: {
				type: 'object',
			},
			gap: {
				type: 'object',
			},
		},
		save: ({ attributes }) => {
			const { columnCount, gap } = attributes;

			const columnStyle = {
				'--arkb-box-w--pc': getColumnBasis(columnCount?.pc),
				'--arkb-box-w--tab': getColumnBasis(columnCount?.tab),
				'--arkb-box-w--mb': getColumnBasis(columnCount?.mobile),
				'--arkb-gap--x': gap?.x,
				'--arkb-gap--y': gap?.y,
			};

			const blockProps = useBlockProps.save({
				className: blockName,
				style: columnStyle || null,
			});

			return (
				<div {...blockProps}>
					<InnerBlocks.Content />
				</div>
			);
		},
	},
	{
		supports: {
			anchor: true,
			className: false,
			align: ['wide'],
		},
		attributes: {
			colPC: {
				type: 'string',
				default: '2',
			},
			colTab: {
				type: 'string',
				default: '2',
			},
			colMobile: {
				type: 'string',
				default: '1',
			},
		},
		migrate: (attributes) => {
			const newAttrs = { ...attributes };

			newAttrs.columnCount = {
				pc: parseInt(attributes.colPC || 2),
				tab: parseInt(attributes.colTab || 2),
				mobile: parseInt(attributes.colMobile || 1),
			};

			return newAttrs;
		},
		save: ({ attributes }) => {
			const { colPC, colTab, colMobile } = attributes;

			const columnStyle = {
				'--arkb-fb': '1' !== colMobile ? getColumnBasis(colMobile) : null,
				'--arkb-fb_tab': '2' !== colTab ? getColumnBasis(colTab) : null,
				'--arkb-fb_pc': '2' !== colPC ? getColumnBasis(colPC) : null,
			};

			const blockProps = useBlockProps.save({
				className: `${blockName} arkb-columns`,
				style: columnStyle || null,
			});

			return (
				<div {...blockProps}>
					<InnerBlocks.Content />
				</div>
			);
		},
	},
	{
		supports: {
			anchor: true,
			className: false,
			align: ['wide'],
		},
		attributes: {
			colPC: {
				type: 'string',
				source: 'attribute',
				selector: '.arkb-columns',
				attribute: 'data-col-pc',
				default: '2',
			},
			colTab: {
				type: 'string',
				source: 'attribute',
				selector: '.arkb-columns',
				attribute: 'data-col-tab',
				default: '2',
			},
			colMobile: {
				type: 'string',
				source: 'attribute',
				selector: '.arkb-columns',
				attribute: 'data-col',
				default: '1',
			},
		},
		save: ({ attributes }) => {
			const basisSet = {
				col1: 100,
				col2: 50,
				col3: 33.33,
				col4: 25,
				col5: 20,
				col6: 16.66,
			};

			const { colPC, colTab, colMobile } = attributes;

			const columnStyle = {
				'--arkb-fb': basisSet[`col${colMobile}`] + '%',
				'--arkb-fb_tab': basisSet[`col${colTab}`] + '%',
				'--arkb-fb_pc': basisSet[`col${colPC}`] + '%',
			};

			const blockProps = useBlockProps.save({
				className: `${blockName} arkb-columns`,
				style: columnStyle || null,
				'data-col': colMobile,
				'data-col-tab': colTab,
				'data-col-pc': colPC,
			});

			return (
				<div {...blockProps}>
					<InnerBlocks.Content />
				</div>
			);
		},
	},
];
